  --_color: inherit;
}
.footer__copy {
  display: flex;
  align-items: flex-end;
  gap: 2rem;
  margin-top: 5rem;
}

/** > 1400px **/
@media (max-width: 87.5rem) {
  .footer__container, .footer__wrapper {
    gap: 1.5rem 1.25rem;
  }
  .footer__container {
    flex-wrap: wrap;
  }
  .footer__picture {
    width: 100%;
  }
}
/** > 1200px **/
@media (max-width: 75rem) {
  .user__btn--desktop {
    display: none;
  }
  .user__btn--mobile {
    --_icon-size: 2.5rem;
    display: inline-flex;
  }
  .burger,
  .menu {
    display: flex;
  }
  .menu__job--catalog {
    display: none;
  }
  [data-menu-overlay] {
    display: block;
    width: 100%;
    height: 100%;
    background-color: var(--background-dark);
    opacity: 0;
    position: fixed;
    z-index: -1;
    inset: 0;
    transition: opacity var(--transition-duration) var(--transition-type);
  }
  [data-menu-overlay=true] {
    cursor: pointer;
    opacity: 0.8;
    z-index: 9;
  }
  .header {
    padding-block: 3rem;
  }
  .header__location {
    display: none;
  }
  .header__end .choices--language,
  .header__end .user {
    display: none;
  }
  .catalog__container {
    display: grid;
    grid-template-columns: minmax(0rem, 75rem);
  }
  .footer {
    padding-block: 3rem;
  }
  .footer__copy {
    margin-top: 3rem;
  }
}
