  }
  .card__swiper {
    margin-inline: -2rem;
  }
  .card__swiper .swiper {
    padding-inline: 2rem;
  }
  .card__swiper .swiper-scrollbar {
    margin: 1rem 2rem 0;
  }
  .card__price {
    padding-inline: 2rem 1rem;
  }
  .card__price-info {
    gap: 1rem;
  }
  .to-top,
  .message {
    --_icon-size: 2.5rem;
    --_padding: 1.25rem;
